.color-bar {
  position: relative;
}

.color-bar__main-color,
.color-bar__second-color {
  position: absolute;
  
  width: 40px;
  height: 40px;
  padding: 0 1px;
}

.color-bar__main-color {
  top: 0px;
  left: 0px;
  z-index: 3;
}

.color-bar__second-color {
  top: 25px;
  left: 25px;
  z-index: 2;
}

.color-bar__button {
  position: relative;
  top: 0;
  left: 0;

  border: none;
  outline: none;
  cursor: pointer;

  &::before {
    position: absolute;
    top: 30px;
    left: 0;


    width: 20px;
    height: 20px;

    content: '';
    background-image: url(./images/swap.svg);
    background-size: 100%;
    outline: none;
    cursor: pointer;
  }
}