.export-menu {
  padding: 10px;

  font-family: Arial, Helvetica, sans-serif;
  font-size: 18px;
  font-weight: normal;
  color: #dddddd;

  border: 2px solid #bbbbbb;
}

.export-menu__input {
  width: 100%;
  margin: 0 0 12px 0;
  padding: 6px 8px;
  box-sizing: border-box;
}

.export-menu__button-group {
  display: flex;
  justify-content: space-between;
}

.export-menu__title{
  margin: 0 0 5px 0;
  padding: 0;
}

.export-menu__button {
  margin: 0 0 10px 0;
  padding: 6px 18px;

  font-size: 14px;
  text-transform: uppercase;

  cursor: pointer;
}