.save-menu {
  display: flex;
  justify-content: space-between;
  margin: 10px 0 0 0;
  padding: 20px 10px;


  color: #dddddd;

  border: 2px solid #bbbbbb;
}

.save-menu__button {
  padding: 5px 10px;

  font-size: 16px;

  border: 0;
  outline: none;

  cursor: pointer;

  &:hover {
    opacity: 0.85;
  }
}