.canvas {
  position: relative;

  width: 640px;
  height: 640px;

  border: 1px solid gray;
}

.canvas__main {
  position: absolute;
  top: 0;
  left: 0;

  width: 640px;
  height: 640px;
  z-index: -2;

  image-rendering: pixelated; 
}

.canvas__drawing {
  position: absolute;
  top: 0;
  left: 0;

  width: 640px;
  height: 640px;
  z-index: -1;

  image-rendering: pixelated; 
}

.canvas__background {
  position: absolute;
  top: 0;
  left: 0;

  width: 640px;
  height: 640px;
  z-index: -3;
  background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAACAAAAAgCAMAAABEpIrGAAAABlBMVEVMTExVVVUnhsEkAAAAHUlEQVR4AWOAAUYoQOePEAUj3v9oYDQ9gMBoegAAJFwCAbLaTIMAAAAASUVORK5CYII=');
  background-repeat: repeat;
}