.landing-page {
  position: absolute;
  top: 53px;
  left: 0;
  z-index: 9999;

  width: 100%;

  font-family: Arial, Helvetica, sans-serif;

  background-color: #1d1d1d;

  &--hidden {
    display: none;
  }
}

.landing-page__wrap {
  max-width: 1670px;
  margin: 0 auto;
}

.landing-page__preview {
  display: flex;
  justify-content: space-around;
  width: 100%;
  padding: 30px 0;

  background-color: #525252;
}

.landing-page__info {
  width: 30%;
  padding: 40px 0 0 0;
}

.landing-page__title {
  font-size: 40px;
  font-weight: normal;
  color: #dddddd;
  
}

.landing-page__button {
  padding: 5px 15px;

  font-size: 34px;
  font-weight: normal;
  color: #333333;
  text-decoration: none;
  
  border-radius: 3px;
  border: none;
  background-color: gold;
  cursor: pointer;
  transition: background-color 0.2s;

  &:hover {
    background-color: #ffffff;
  }
}

.landing-page__screen {
  width: 50%;
}

.landing-page__functionality {
  display: flex;
  justify-content: space-around;

  font-size: 24px;
  font-weight: normal;
  color: #dddddd;

  background-color: #222222;
}

.animation {
  display: flex;
  justify-content: space-around;
  padding: 0 0 30px 0;
}

.animation__list {
  display: flex;
  margin: 0;
  padding: 0;
}

.animation__title {
  text-align: center;
  font-size: 24px;
  font-weight: normal;
  color: #dddddd;
}

.animation__item {
  margin: 0 10px 0 0;

  list-style: none;
}

.animation__image {
  width: 128px;
  height: 128px;

  background-color: #dddddd;
}

.landing-page__functionality {
  padding: 0 0 30px 0;
}

.functionality__list {
  margin: 0 0 0 30px;
  padding: 0;
}

.functionality__item {
  position: relative;
  margin: 0 0 7px 0;

  list-style: none;

  &::before {
    position: absolute;
    top: 0;
    left: -35px;
    z-index: 5;

    width: 25px;
    height: 25px;

    background-size: 100%;
    content: '';
  }
}

.functionality__item--tools::before {
  background-image: url(./images/sketch.svg);
}

.functionality__item--export::before {
  background-image: url(./images/export-file.svg);
}

.functionality__item--save::before {
  background-image: url(./images/save.svg);
}

.functionality__item--maximize::before {
  background-image: url(./images/maximize.svg);
}

.functionality__item--google::before {
  background-image: url(./images/google.svg);
}

.functionality__item--preview::before {
  background-image: url(./images/preview.svg);
}

.landing-page__copyright {
  font-size: 18px;
  font-weight: normal;
  color: #dddddd;
  text-align: center;
}