.header {
  font-family: Arial, Helvetica, sans-serif;
  background-color: #333333;
}

.header__body {
  display: flex;
  justify-content: space-between;
  max-width: 1400px;
  margin: 0 auto;
  padding: 10px 1%;
}

.header__link {
  margin: 0;

  font-size: 26px;
  line-height: normal;
  color: #dddddd;
  text-decoration: none;
}