.frames {
  display: inline-block;
  width: 150px;
  max-height: 620px;

  overflow-y: auto;
  overflow-x: hidden;

  -webkit-user-select: none;
}

.frames__list {
  margin: 0;
  padding: 0;
}

.frames__item {
  margin: 0 0 10px 0;
  height: 132px;

  list-style: none;
}

.frames__button {
  width: 132px;

  border: 2px solid gray;
  padding: 10px 0;
  cursor: pointer;
}

.frames__drag {
  position: relative;
  z-index: 3;
  width: 134px;
  height: 134px;
}

.frames__drag-button {
  position: absolute;
  left: 3px;
  bottom: 3px;

  display: none;
  width: 30px;
  height: 30px;

  border: 0;
  outline: none;
  cursor: pointer;
  background: none;
  background-image: url(./images/move.svg);
  background-color: gray;
  background-size: 80%;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  opacity: 0.8;
}

.frames__drag:hover .frames__drag-button {
  display: block;
  z-index: 7;
}