.frame {
  position: relative;

  width: 128px;
  height: 128px;

  border: 3px solid #444444;
  border-radius: 3px;
  background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAACAAAAAgCAMAAABEpIrGAAAABlBMVEVMTExVVVUnhsEkAAAAHUlEQVR4AWOAAUYoQOePEAUj3v9oYDQ9gMBoegAAJFwCAbLaTIMAAAAASUVORK5CYII=');
  background-repeat: repeat;

  &:not(&--active):hover {
    border: 3px solid #aaaaaa;
  }

  &--active {
    border: 3px solid yellow;
  }
}

.frame__body {
  padding: 0;

  background: transparent;
  border: 0;
  outline: none;
}

.frame__canvas {
  width: 128px;
  height: 128px;

  image-rendering: pixelated;
}

.frame:hover .frame__button {
  display: block;
}

.frame__image {
  width: 128px;
  height: 128px;
}

.frame__index {
  position: absolute;
  top: 0;
  left: 0;

  width: 30px;
  height: 30px;
  margin: 0;

  line-height: 30px;
  text-align: center;
  vertical-align: middle;

  border-radius: 1px;
  background-color: yellow;
}

.frame__button {
  position: absolute;
  z-index: 3;

  display: none;
  width: 30px;
  height: 30px;

  border: none;
  border-radius: 1px;
  background-color: gray;
  background-size: 70%;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  opacity: 0.8;
  cursor: pointer;

  &--delete {
    top: 0;
    right: 0;

    background-image: url(./images/garbage.svg);
  }

  &--copy {
    bottom: 0;
    right: 0;

    background-image: url(./images/copy.svg);
  }

  &--swap {
    bottom: 0;
    left: 0;

    background-image: url(./images/move.svg);
  }
}