.preview-fullscreen {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;

  display: none;
  width: 100%;
  height: 100%;

  background-color: gray;
  &--active {
    display: flex;
  }
}

.preview-fullscreen__body {
  position: absolute;
  left: calc(50% - 320px);
  top: calc(50% - 320px);

  width: 640px;
  height: 640px;
}

.preview-fullscreen__canvas {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;

  width: 640px;
  height: 640px;

  image-rendering: pixelated;
}

.preview-fullscreen__background {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;

  width: 640px;
  height: 640px;

  background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAACAAAAAgCAMAAABEpIrGAAAABlBMVEVMTExVVVUnhsEkAAAAHUlEQVR4AWOAAUYoQOePEAUj3v9oYDQ9gMBoegAAJFwCAbLaTIMAAAAASUVORK5CYII=');
  background-repeat: repeat;
}