.preview {
  position: relative;
}

.preview__canvas {
  position: relative;
  z-index: -1;

  width: 212px;
  height: 212px;

  image-rendering: pixelated;
}

.preview__background {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -2;

  width: 212px;
  height: 212px;

  background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAACAAAAAgCAMAAABEpIrGAAAABlBMVEVMTExVVVUnhsEkAAAAHUlEQVR4AWOAAUYoQOePEAUj3v9oYDQ9gMBoegAAJFwCAbLaTIMAAAAASUVORK5CYII=');
  background-repeat: repeat;
}

.preview__control {
  display: flex;
  justify-content: flex-end;
}

.preview__value {
  margin: 5px;

  font-size: 18px;
  color: #ffffff;
}

.preview__button {
  position: absolute;
  top: 10px;
  right: 10px;

  padding: 0;

  border: 0;
  outline: none;
  background: transparent;
  cursor: pointer;
}

.preview__icon {
  width: 20px;
  height: 20px;

  &:hover {
    transform: scale(1.1);
    transition: transform 0.1s;
  }
}

.preview-fullscreen {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;

  display: none;
  width: 100%;
  height: 100%;

  background-color: gray;
  &--active {
    display: flex;
  }
}