.tool-bar {
  display: inline-block;
}

.tool-bar__list {
  display: flex;
  flex-wrap: wrap;
  width: 110px;
  margin: 0;
  padding: 0;
}

.tool-bar__item {
  position: relative;

  width: 50px;
  height: 50px;
  margin: 0 0 2px 0;

  list-style: none;

  &:nth-child(2n + 1) {
    margin: 0 2px 2px 0;
  }
}

.tool {
  position: relative;

  width: 50px;
  height: 50px;
  padding: 0;

  background-color: #444444;
  background-repeat: no-repeat;
  background-size: 45%;
  background-position: 50% 50%;
  background-image: url(./images/pen.svg);
  border: 2px solid #bbbbbb;
  outline: none;
  cursor: pointer;

  &--active {
    border: 2px solid yellow;
  }
}

.tool--pen {
  background-image: url(./images/pen.svg);
}

.tool--fill {
  background-image: url(./images/fill.svg);
}

.tool--fill-all {
  background-image: url(./images/paint-roller.svg);
}

.tool--eraser {
  background-image: url(./images/eraser.svg);
}

.tool--line {
  background-image: url(./images/line.svg);
}

.tool--pipette {
  background-image: url(./images/pipette.svg);
}