.pen-size-panel__list {
  display: flex;
  margin: 0;
  margin: 0 0 3px 0;
  padding: 0;
}

.pen-size-panel__item {
  list-style: none;

  &:not(:last-child) {
    margin-right: 2px;
  }
}

.pen-size-panel__button {
  width: 24px;
  height: 24px;
  padding: 0;

  font-size: 15px;
  color: #dddddd;
  
  border: 2px solid #bbbbbb;
  background-color: #444444;
  outline: none;
  cursor: pointer;

  &--active {
    border: 2px solid yellow;
    color: yellow;
  }
}